<template>
  <div class="container w100 h100 bgf5">
    <form action="" class="pay-offline bgf p2">
      <div class="form-group">
        <label></label>
      </div>
      <div class="form-group">
        <label></label>
        <a href="javascript:void(0)" class="cc" @click="setDate()">{{messagedate}}</a>
      </div>
      <div class="form-group">
        <label></label>
        <input type="number" placeholder="" v-model="form.bank_flow">
      </div>
      <div class="form-group">
        <label></label>
        <input type="text" placeholder="" v-model="form.name">
      </div>
      <div class="form-group">
        <label></label>
        <input type="number" placeholder="" v-model="form.bank_account">
      </div>
      <div class="form-group">
        <label></label>
        <input type="text" placeholder="  " v-model="form.bank_name">
      </div>
    </form>
    <div class="bank-info bgf mt5 p2">
      <p class="c9 offline-title"></p>
      <div class="g-bank-info">
        <p></p>
        <span></span>
      </div>
      <div class="g-bank-info">
        <p></p>
        <span>03811000040037226</span>
      </div>
      <div class="g-bank-info">
        <p></p>
        <span></span>
      </div>
      <div class="g-bank-notice c9">
        <p class="offline-title mt10">:</p>
        <p>1) </p>
        <p>2) </p>
        <p>3) </p>
        <p>4) 3-5</p>
        <div class="bank-info-last tac mt10">
          <p></p>
          <p>400-8888-3003</p>
        </div>
      </div>
    </div>
    <a href="javascript:void(0)" class="btn-order pf w100" @click="alreadypay()"></a>

  </div>
</template>

<script>
  import api from '../../services/api';
  import userinfo from '../../services/userinfo'
  import error from '../../services/error'
  import cookie from '../../tools/cookie'
  import page from '../page'
  import hook from '../../tools/hook'
  import lstore from '../../tools/lstore'
  var method = {};
  var store = {};
  store.form = {trade_sn:'',bank_flow:'',name:'',bank_account:'',bank_name:'',pay_date:''};
  store.messagedate = '';
  store.data = '';
  method.setDate = function () {
    var self = this;
    console.log('run');
    this.$calendar.show({
      onOk(data){
        store.data= data.year+'-'+data.month+'-'+data.day;
        store.messagedate = store.data;
        store.form.pay_date = store.data;
      },
      onCancel(){
        console.log('123')
      },
      year:2017,
      month:8,
      day:20
    })
  };
  method.alreadypay = function () {
    var self = this;
    api.offline(store.form).then(function (res) {
      if (error.success == res.code) {
        self.$router.push({
          path: '/allOrders'
        });
      }
    })
  };
  export default {
    name:'payOffline',
    data(){
        return store
    },
    methods:method,
    mounted:function () {
      try {
        store.form.trade_sn = lstore.get_item('trade_sn').val;
      } catch (e) {

      }
    }
  }
</script>



